.contacts__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  align-items: center;
  padding: 120px 0;

  @include vp-1366 {
    gap: 30px;
    padding: 100px 0;
  }

  @include vp-768 {
    display: flex;
    flex-direction: column;
    gap: 35px;
    align-items: flex-start;
    padding: 58px 0 65px;
  }
}

.contacts__map-wrapper {
  height: 306px;

  @include vp-1366 {
    height: 335px;
  }

  @include vp-768 {
    width: 100%;
    order: 1;
    height: 316px;
  }
}

.contacts__map {
  width: 100%;
  height: 100%;
  border: none;
}

.contacts__info {
  padding-left: 60px;

  @include vp-1366 {
    padding: 0;
  }

  @include vp-768 {
    order: 0;
  }

  p {
    font-family: $ff-medium;
    font-size: 18px;
    line-height: 27px;
    color: $dark;
    margin: 0 0 30px;

    @include vp-768 {
      margin: 0 0 28px;
    }
  }

  address,
  a {
    position: relative;
    font-family: $ff-regular;
    font-style: normal;
    font-size: 16px;
    line-height: 24px;
    color: $dark;
    text-decoration: none;
    margin: 0 0 20px;
    padding-left: 28px;
    letter-spacing: normal;

    &::before {
      position: absolute;
      content: "";
      width: 22px;
      height: 22px;
      left: 0;
      top: 0;
    }
  }

  a {
    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  address::before {
    background: left top no-repeat url("../img/location.svg");
  }

  a::before {
    background: left top no-repeat url("../img/phone.svg");
  }
}
